import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder ,FormControl, Validators } from '@angular/forms';
import { Router,ActivatedRoute} from '@angular/router';
import { ClientService } from '../../../services/client.service';
declare let alertify: any;

@Component({
  selector: 'app-client-market-wallet-form',
  templateUrl: './client-market-wallet-form.component.html',
  styleUrls: ['./client-market-wallet-form.component.css']
})
export class ClientMarketWalletFormComponent implements OnInit {
  public clientForm : FormGroup;
  public formErrors:any = {};
  private isNew:Boolean = true;
  selectedWallet;
  selectedMarket;
  
  constructor(public router: Router, public clientApi: ClientService, public route:ActivatedRoute, public fb : FormBuilder) { }

  ngOnInit() {
  	this.createForm();
    if(this.route.snapshot.routeConfig.path == "new"){
      this.isNew=true;
    }
    else{
      this.isNew=false;
      if(this.route.snapshot.params.id){
        let id = this.route.snapshot.params.id;
        this.clientApi.getClientMarketWallet(id,'client-market-wallet').subscribe(result => {
          if(result['success'] && result['data']){
            let data = result['data'];
            this.clientForm.setValue(data);
            this.selectedMarket = result['market_list'];
            this.selectedWallet = result['wallet_list'];
          }
          else{
            this.router.navigate(['/client/marketWalletList']);
            alertify.error('Record Not Found');
          }
        });
      }
    }
  }

  createForm() {
    this.clientForm = this.fb.group({
      id              : [''],
      wallet_id       : [''],
      market_id       : [''],
      client_id       : [''],
      registration_fee: ['0', [Validators.required, Validators.min(0)]],
      status          : ['1',Validators.required],
      enable_trade    : ['0',Validators.required]
    });
  }

  onSubmit()
  {
    let me = this;
    if(!this.clientForm.valid){
      this.formErrors = {};
      /* IF FORM IS INVALID SET ERROR MESSAGE */
      this.setFormErrorMsg();
      // this.validateAllFormFields(this.staffDetailForm);
    }
    else{
      var body = {
        'MarketWallet[id]'              :me.clientForm.value.id,
        'MarketWallet[wallet_id]'       :me.clientForm.value.wallet_id,
        'MarketWallet[market_id]'       :me.clientForm.value.market_id,
        'MarketWallet[client_id]'       :me.clientForm.value.client_id,
        'MarketWallet[registration_fee]':me.clientForm.value.registration_fee,
        'MarketWallet[is_locked]'       :me.clientForm.value.status ? 0 : 1,
        'MarketWallet[enable_trade]'    :me.clientForm.value.enable_trade ? 1 : 0 
      }
      if(!this.isNew){
        body['MarketWallet[id]'] = me.clientForm.value.id;
      }
      this.clientApi.saveMarketWallet(body).subscribe(response =>{
        if(response['success'])
        {
          this.router.navigate(['client/marketWalletList']);
            alertify.success(response['message']);
        }
        else{
            alertify.error(response['message']);
        }
      });
    }
  }

  setFormErrorMsg(){
    let errors = this.getFormErrors(this.clientForm);

    for(let key in errors){
      if(errors[key] != null){
        let obj:any = errors[key];
        if(obj.hasOwnProperty('required')){
          this.formErrors[key] = 'This field is required.';
        }
        else if(obj.hasOwnProperty('min')){
          this.formErrors[key] = 'This field\'s value must be greater or equal to ' + obj['min']['min'] + '.';
        }
      }
    }
  }

  getFormErrors(formGroup: FormGroup){
    let errors: any = {};
    Object.keys(formGroup.controls).forEach(field => {
      const control = formGroup.get(field);
      if (control instanceof FormControl) {
        errors[field] = control.errors;
      } else if (control instanceof FormGroup) {
        errors[field] = this.getFormErrors(control);
      }
    });
    return errors;  
  }

  onCancelClick()
  {
    let me = this;
    var message = "Do you want to cancel?";
    alertify.confirm(message, function(e) {
        if(e) {
            me.router.navigate(['/client/marketWalletList']);
        }else{
          // debugger;
        }
      })
  }
}
