import { Injectable } from '@angular/core';
import { HttpService } from './http.service';

@Injectable({
  providedIn: 'root'
})
export class HomeService {

  constructor(public apiHttp:HttpService) { }

  public stackedChart(params) {
    let url = 'backend/dashboard/stacked-chart';
    return this.apiHttp.getRequest(url, params);
  }

  totalRecord() {
    let url = 'backend/dashboard/total-count';
    return this.apiHttp.getRequest(url, {});
  }
}
