import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { AppComponent } from './app.component';
import { AppRoutingModule } from './app-routing.module';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';

import { WalletModule } from './components/wallet/wallet.module';
import { MarketModule } from './components/market/market.module';
import { ClientModule } from './components/client/client.module';

import { HomeComponent } from './components/home/home.component';
import { HttpService } from './services/http.service';
import { DataTablesModule } from 'angular-datatables';
import { NgxMaskModule } from 'ngx-mask';
import { ModalComponent } from './components/ng-modal/modal.component';
import { TradeHistoryComponent } from './components/trade-history/trade-history.component';
import { OrderDetailModalComponent } from './components/order-detail-modal/order-detail-modal.component';
import { OrderCancelModalComponent } from './components/order-cancel-modal/order-cancel-modal.component';
import { NodeServerComponent } from './components/node-server/node-server.component';
import { NetworkSettingsComponent } from './components/network-settings/network-settings.component';

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    ModalComponent,
    TradeHistoryComponent,
    OrderDetailModalComponent,
    OrderCancelModalComponent,
    NodeServerComponent,
    NetworkSettingsComponent,
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    WalletModule,
    MarketModule,
    ClientModule,
    HttpClientModule,
    DataTablesModule,
    FormsModule,
    ReactiveFormsModule,
    NgxMaskModule.forRoot()
  ],
  providers: [HttpService],
  bootstrap: [AppComponent]
})
export class AppModule { }
