import { AfterViewInit, Component, OnInit, ViewChild } from '@angular/core';
import { ClientService } from '../../../services/client.service';
import { WalletService } from '../../../services/wallet.service';
import { Router,ActivatedRoute } from '@angular/router';
import DataTables from 'datatables.net-dt';
import { DataTableDirective } from 'angular-datatables';
import { PAGE_PER_SIZE} from '../../../config/constant';
declare var alertify:any;
declare var $:any;
@Component({
  selector: 'app-client-wallet-list',
  templateUrl: './client-wallet-list.component.html',
  styleUrls: ['./client-wallet-list.component.css']
})
export class ClientWalletListComponent implements OnInit, AfterViewInit {
  @ViewChild(DataTableDirective)
  private datatableElement: DataTables;
  clientWalletList        = [];
	moveclientWalletList    = [];
  clientList              = [];
  walletList              = [];
  selectedClient:any = {};
  activeRowIdx:number     = 0;
  showLoadingGIf: boolean;
  showClientWalletGif: boolean;
  mask_txt = 'Loading...';

  public dtOptions: DataTables.Settings = {};
  public cols = [
    {title : 'Id', data: 'id', visible : false},
    {title : 'Client', data: 'client_name_en'}
  ]

  constructor(private route:Router,
              public path     :ActivatedRoute,
              public clientApi:ClientService,
              public walletApi:WalletService) { }

  ngOnInit() {
    // GET WALLET MASTER LIST
    this.getWalletMasterList();
    // this.getClientMasterList();
    this.dtOptions = {
      pagingType: 'simple',
      pageLength: PAGE_PER_SIZE,
      serverSide: true,
      processing: true,
      bInfo : false,
      bLengthChange : true,
      aaSorting: [[ 0, "desc" ]],
      lengthMenu: [ [10, 25, 50,100,500], [10, 25, 50,100,500] ],
      language: {
        "zeroRecords" : " ",
        "emptyTable"  : "No records found",
        "search"      : " ",
        "paginate"    : {
          "previous"  : "<i class='fa fa-chevron-circle-left' aria-hidden='true'></i>",
          "next"      : "<i class='fa fa-chevron-circle-right' aria-hidden='true'></i>"
        },
        "lengthMenu": "Page Size _MENU_ ",
      },
      dom: 'rt<"clear"><"bottom"lip><"clear">',
      ajax: (dataTablesParameters: any, callback) => {
        dataTablesParameters['pnl_id'] = 2;
        let params:any = {pnl_id : 2};
        if (this.path.snapshot.params.id){
          dataTablesParameters['id'] = parseInt(this.path.snapshot.params.id);
        }
          this.clientApi.getClientList(dataTablesParameters).subscribe(res => {
            this.clientList       = res['data'];
            this.selectedClient   = res['data'].length > 0 ? res['data'][0] : {};
            this.showLoadingGIf = false;
            this.getClientWalletList();
          // this.selectedClient['id'] = 0;
          // if(res['data'].length != 0){

          //   if (this.path.snapshot.params.id){
          //     let id = this.path.snapshot.params.id;
          //     // this.clientApi.getClientData(id,'client').subscribe(result => {
          //     //    this.clientList     = [result['data']];
          //     //    this.selectedClient = result['data'];
          //     //    this.getClientWalletList();
          //     // });
          //     this.clientApi.getClientList(params).subscribe(res=>{
          //       this.clientList       = res['data'];
          //       this.selectedClient   = res['data'][0];
          //       this.showLoadingGIf = false;
          //       this.getClientWalletList();
          //     });

          //   }
          //   else{
          //     this.clientList       = res['data'];
          //     this.selectedClient   = res['data'][0];
          //     this.activeRowIdx = 0;
          //     // $('#client-name').val(res.data[0].client_name_en);
          //     this.getClientWalletList();
          //   }
          // }

          callback({
            recordsTotal: res.recordsTotal,
            recordsFiltered: res.recordsFiltered,
            data: []
          });
          $('#client-table tfoot tr').appendTo('#client-table thead');
        })
      },
      columns: this.cols
    };



  	// this.getclientWalletList();
  }

  ngAfterViewInit(): void {
    var me = this;
    this.datatableElement.dtInstance.then((dtInstance: DataTables.Api) => {
      dtInstance.columns().every(function () {
        const that = this;
        $('input').on('keyup', function (e) {
          if(e.keyCode == 13){ //on enter press
            if (that.search() !== this['value']) {
              dtInstance.cell(that).search(this['value']).draw();
            }
          }else if(e.keyCode == 8 || e.keyCode == 46){ //on backspace or del click
            if((this['value']) == ''){
              //reload datatable
              dtInstance.cell(that).search('').draw();
            }
          }
        });
      });
    });
  }

  getClientMasterList() {
    this.showLoadingGIf = true;
    this.showClientWalletGif = true;
    let params:any = {pnl_id : 2};
    this.clientApi.getClientList(params).subscribe(res=>{
      this.clientList       = res['data'];
      this.selectedClient   = res['data'][0];
      this.showLoadingGIf = false;
      this.getClientWalletList();
    });

  }

  getClientWalletList() {
    this.showClientWalletGif = true;
    let params:any = {
      client_id  : this.selectedClient['id'],
      pnl_id     : 1
    }
    this.clientApi.getClientWallet(params).subscribe(res=>{
      this.clientWalletList   = res["data"];
      this.showClientWalletGif = false;
    });
  }

  getWalletMasterList() {
    let params:any = {pnl_id : 2};
    this.walletApi.getWalletList(params).subscribe(
      res=>{
        this.walletList = res['data'];
      });
  }



  edit(id) {
    if(id==null)
    {
      alertify.error('Please save the record');
    }
    else{
      this.route.navigate([`client/wallet/edit/${id}`]);
    }
  }

  deleteItem(idx) {
    alertify.confirm('Do you want to delete this record?', ()=>{
      this.clientWalletList[idx]['is_deleted']=1;
    });
  }

  moveWallet(item) {
    let obj = {
        client_id: this.selectedClient.id,
        client_name_en: this.selectedClient.client_name_en,
        code: item.code,
        name: item.name,
        wallet_id: item.id,
        registration_fee:item.registration_fee,
        is_new : 1,
        is_deleted : 0,
        is_locked: 0
      };
      if(this.clientWalletList.some(e=>e.client_id===obj.client_id && e.wallet_id===item.id)){
        alertify.error('Record Already Exist');
      } else {
        this.clientWalletList.push(obj);
      }
  }
  onClientItemClick(item,indx) {
    // $('#client-name').val(item.client_name_en);
    this.selectedClient = item;
    this.activeRowIdx = indx;
    this.getClientWalletList();
  }

  saveClientWallet() {
    let newRecords = this.clientWalletList.filter(function(item){
      return item.is_new == 1;
    });

    let deletedRecords = this.clientWalletList.filter(function(item){
      return item.is_deleted==1;
    });

    let records = {new_records:JSON.stringify(newRecords),deleted_records:JSON.stringify(deletedRecords)};

    this.clientApi.saveCWallet(records).subscribe(response=>{
      if(response['success']){
        alertify.success(response['message']);
        this.getClientWalletList();
      }
      else{
        alertify.error(response['message']);
      }
      //TODO
    });
   }

   onOffStatusChange(id)
   {
     let controllerName = 'client-wallet';
      this.clientApi.toggleStatus(id,controllerName).subscribe(res=>{
        if(res['success']==true)
        {
          alertify.success(res['message']);
        }
        else{
          alertify.error(res['message']);
          this.getClientWalletList();
        }
      });
   }
}
