import { Component, OnInit } from '@angular/core';
import { MarketService } from '../../../services/market.service';
import { WalletService } from '../../../services/wallet.service';
import { Router } from '@angular/router';
declare var alertify:any;
declare var $:any;

@Component({
  selector: 'app-market-wallet-list',
  templateUrl: './market-wallet-list.component.html',
  styleUrls: ['./market-wallet-list.component.css']
})
export class MarketWalletListComponent implements OnInit
{
  public marketWalletList       = [];
  public marketList: Array<any> = [];
  public walletList             = [];
  public selectedMarket: any    = {};
  public activeRowIdx: number   = 0;
  public showLoadingGIf: boolean;
  public showMarketWalletGif: boolean;
  public mask_text: string = "Loading...";
  public verifyLoadingImgRowIdx:number= null;
  public row:any;

  constructor(public route:Router, public marketApi:MarketService, walletApi: WalletService ) { }

  ngOnInit()
  {
    this.getMarketList();
    this.getWalletList();
  }

  /** [getMarketWalletList fetch marketWallet rec by market id] */
  getMarketWalletList()
  {
    this.showMarketWalletGif = true;
    let params:any = {
      market_id : this.selectedMarket['id']
    };
    this.marketApi.getMarketWalletList(params).subscribe(res=>{
      this.marketWalletList=res["data"];
      this.showMarketWalletGif = false;
    });
  }

  /**
   * [edit navigate to MarketWallet edit form]
   * @param {[number]} [marketWallet Id]
   */
  edit(id) {
    if(id == null){
      alertify.error('Please save the record');
    }else{
      this.route.navigate([`market/walletList/edit/${id}`]);
    }
  }

  deleteItem(idx) {
    alertify.confirm('Do you want to delete this record?', (res) => {
      const rec:any = this.marketWalletList[idx];
      if(rec.hasOwnProperty('id') && rec['id'] > 0){
        this.marketWalletList[idx]['is_deleted'] = 1;
      }
      else{
        this.marketWalletList.splice(idx, 1);
      }

    })
  }

  /** [getMarketList fetch market list by pnl_id] */
  getMarketList() {
    this.showLoadingGIf = true;
    this.showMarketWalletGif = true;
    let obj:any =  {'pnl_id': 2 };
    this.marketApi.marketMaster(obj).subscribe(res=>{
      this.marketList     = res['data'];
      this.selectedMarket = res['data'][0];
      this.showLoadingGIf = false;
      this.getMarketWalletList();
    })
  }

  moveItem(item) {
    let obj = {
      market_id: this.selectedMarket.id,
      name: item.name,
      wallet_id: item.id,
      market_name_en:this.selectedMarket.market_name_en,
      registration_fee:item.registration_fee,
      is_new : 1,
      is_deleted : 0,
      is_locked: 0,
      verifyData:null
    };
    if(this.marketWalletList.some(e=>e.wallet_id===obj.wallet_id && e.market_id===obj.market_id)){
      alertify.error('Record Already Exist');
    }
    else {
      this.marketWalletList.push(obj);
      this.verifyLoadingImgRowIdx = this.marketWalletList.length-1;
      let params={
        makrket_code: this.selectedMarket.code,
        wallet_code:item.name,
        rowIdx:this.marketWalletList.length-1,
      }
      this.marketApi.verifyMarketWallet(params).subscribe(result=>{
        this.verifyLoadingImgRowIdx = null;
        if(result['success']==true){
          this.marketWalletList[this.marketWalletList.length-1]['verifyData']=1;
        }else{
          this.marketWalletList[this.marketWalletList.length-1]['verifyData']=0;
        }
        this.row=result['rowId'];
      });
    }
  }

  onSelectedMarket(item,indx) {
    this.showMarketWalletGif = true;
    this.selectedMarket   = item;
    this.activeRowIdx     = indx;
    this.getMarketWalletList();
  }

  /** [getWalletList fetch wallet rec by pnl_id] */
  getWalletList() {
    let obj:any =  {
        'pnl_id':1
    };

    this.marketApi.walletMaster(obj).subscribe(res=>{
      this.walletList = res['data'];
    })
  }

  saveWallet() {
    var me = this;
    let newRecords = this.marketWalletList.filter(function(item){
      if(item.verifyData==1){
        return item.is_new == 1;
      }
    });
    let deletedRecords = this.marketWalletList.filter(function(item){
      return item.is_deleted==1;
    });

    let records = {new_records:JSON.stringify(newRecords),deleted_records:JSON.stringify(deletedRecords)};
    this.marketWalletList.filter(function(item){
      // if(item.verifyData == 0){
          let msg = "Only verified records wiil be saved. Do you still want to continue ?";
          alertify.confirm(msg, function(e) {
          if(e) {
            if(records['new_records'] == '[]' && records['deleted_records'] == '[]'){
              alertify.error('No records to save');
              me.getMarketWalletList();
            }else{
              me.marketApi.saveMarketWallet(records).subscribe(response=>{
                if(response['success']){
                  alertify.success(response['message']);
                  me.getMarketWalletList();
                }
                else{
                  alertify.error(response['message']);
                }
                //TODO
              });
            }
          }
        });
      /*}*//*else{
        me.marketApi.saveMarketWallet(records).subscribe(response=>{
          if(response['success']){
            alertify.success(response['message']);
            me.getMarketWalletList();
          }
          else{
            alertify.error(response['message']);
          }
          //TODO
        });
      }*/
    })
  }

  /**
   * [onOffStatusChange change marketWallet is_locked to 1/0]
   * @param {[number]} [market Wallet id]
   */
  onOffStatusChange(id)
  {
    if(id == null){
      alertify.error('Please save the record');
    }else{
      let controllerName = 'market-wallet';
      this.marketApi.toggleStatus(id,controllerName).subscribe(res=>{
        if(res['success']==true)
        {
          alertify.success(res['message']);
        }
        else{
          alertify.error(res['message']);
          this.getMarketWalletList();
        }
      });
    }
  }

}
