import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HomeComponent } from './components/home/home.component';
import { WalletModule } from './components/wallet/wallet.module';
import { MarketModule } from './components/market/market.module';
import { ClientModule } from './components/client/client.module';
import { TradeHistoryComponent } from './components/trade-history/trade-history.component';
import { NodeServerComponent } from './components/node-server/node-server.component';
import { NetworkSettingsComponent } from './components/network-settings/network-settings.component';

const routes: Routes = [
		{ path : '' , redirectTo: 'dashboard', pathMatch: 'full'},
		{ path : 'dashboard' , component : HomeComponent},
		{ path : 'wallet', loadChildren: () => WalletModule},
		{ path : 'market', loadChildren: () => MarketModule},
		{ path : 'client', loadChildren: () => ClientModule},
		{ path : 'tradeHistory' , component : TradeHistoryComponent},
		{ path : 'nodeServer' , component : NodeServerComponent},
		{ path : 'networkSettings' , component : NetworkSettingsComponent},
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {useHash : true})],
  exports: [ RouterModule],
  declarations: []
})
export class AppRoutingModule { }
