import { Component, OnInit, ViewChild } from '@angular/core';
import { WalletService } from '../../../services/wallet.service';
import DataTables from 'datatables.net-dt';
import { DataTableDirective } from 'angular-datatables';
import { PAGE_PER_SIZE} from '../../../config/constant';
import { Router } from '@angular/router';
declare let alertify: any;

@Component({
  selector: 'app-wallet-list',
  templateUrl: './wallet-list.component.html',
  styleUrls: ['./wallet-list.component.css']
})
export class WalletListComponent implements OnInit
{
  @ViewChild(DataTableDirective)
  public datatableElement: DataTables;
  public walletlist: Array<any> = [];
  public dtOptions: DataTables.Settings = {};
  public cols = [
    { title:'Code', data: 'code'}, 
    { title:'Name', data: 'name' }, 
    { title:'Description', data: 'description' },
    { title:'Registration Fee', data: 'registration_fee' },
    { title:'Status', data: 'is_locked', sortable:false  },
    { title:'Action', data:'id', sortable:false }
   ];
  constructor(public router:Router, public walletApi:WalletService) { }

  ngOnInit(){
    this.dtOptions = {
      pagingType: 'simple_numbers',
      pageLength: PAGE_PER_SIZE,
      serverSide: true,
      processing: true,
      bLengthChange: true,
      aaSorting: [[ 3, "desc" ]],
      lengthMenu: [ [10, 25, 50,100,500], [10, 25, 50,100,500] ],
      language: {
        "zeroRecords": " ",
        "search":" ",
        "emptyTable": "No records found",
        "lengthMenu": "Page Size _MENU_ "
      },
      dom: 'rt<"clear"><"bottom"lip><"clear">',
      ajax: (tablesParameters: any, callback) => {
        tablesParameters.pnl_id = 1;
        this.walletApi.getWalletList(tablesParameters).subscribe(res => {
          this.walletlist = res.data;
          callback({
            recordsTotal: res.recordsTotal,
            recordsFiltered: res.recordsFiltered,
            data: []
          });
        })
      },
      columns:this.cols
    };
  }

  // ngAfterViewInit(): void {
  //   this.datatableElement.dtInstance.then((dtInstance: DataTables.Api) => {
  //     dtInstance.columns().every(function () {
  //       const that = this;
  //       $('input', this.footer()).on('keyup change', function (i) {
  //         if (that.search() !== this['value']) {
  //           dtInstance
  //             .cell(that)
  //             .search(this['value'])
  //             .draw();
  //         }
  //       });
  //     });
  //   });
  // }
 /**[getWalletList fetch wallet list and fields from pnl_id 1]*/
  // getWalletList(){
  //   let params:any = {'pnl_id' : 1};
  //   this.walletApi.getWalletList(params).subscribe(res=>{
  //     this.walletlist=res["data"];
  //   }); 
  // }
  /**
   * [edit on edit redirect from list to wallet/editform]
   * @param {[number]} id [wallet id to fetch/set record]
   */
  edit(id){
    this.router.navigate([`wallet/edit/${id}`]);
  }

  /**
   * [delete delete a wallet record by id]
   * @param {[number]} id [wallet id]
   */
  delete(id){
    let me = this;
    var message = "Do you want to delete this record?";
    alertify.confirm(message, function(e) {
      if(e) {
        me.walletApi.deleteWallet(id).subscribe(response =>{
          if(response['success'] == true){
              me.reloadDataTable();
              alertify.success(response['message']);
            }else{
              alertify.error(response['message']);
            }
          });
      }else{
        // 
      }
    })
  }

  /**
   * [onOffStatusChange (changes base_wallet_Master is_locked to 1 inactive/0 active)]
   * @param {[number]} id [wallet id]
   */
  onOffStatusChange(id)
  {
    let me = this;
    this.walletApi.toggleStatus(id).subscribe(response=>{
      if(response['success']==true)
      {
        alertify.success(response['message']);
        me.reloadDataTable();
      }
      else{
        alertify.error(response['message']);
      }
    });
  }

  reloadDataTable(){
    this.datatableElement.dtInstance.then((dtInstance: DataTables.Api) => {
        dtInstance.draw();
      });
  }

}
