import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';

import { WalletListComponent } from './wallet-list/wallet-list.component';
import { WalletFormComponent } from './wallet-form/wallet-form.component';

const routes: Routes = [
	{ path : '', redirectTo: 'list', pathMatch : 'full' }, // default load Walletlist which loads below path
	{ path : 'list', component : WalletListComponent, pathMatch : 'prefix' },// load list
	{ path : 'delete/:id', component : WalletListComponent, pathMatch : 'prefix' }, // for delete path but component not needed so WalletList Component used for now
	{ path : 'new', component : WalletFormComponent, pathMatch : 'prefix' }, // for new record insertion WalletForm
	{ path: 'edit/:id', component: WalletFormComponent } // for edit but use same WalletForm Component
];
@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class WalletRoutingModule { }
