export interface ClientSelectionItem {
    id: number;
    client_code: string;
    client_name_en: string;
    client_name_ja: string;
}

export class PaginationConfig {
  pnl_id     : number;
	pageSize   : number;
	page       : number;
	totalPages : number;
	isLast     : boolean;
}


export class ClientPicker {
	state: string;
  selectionItems: Array<ClientSelectionItem>;
  selectedRecord: ClientSelectionItem;
  selectedId: number;
  highlightedRecord: ClientSelectionItem;
  highlightedId: number;
  displayName: string;
  highlightedRowIdx: number;
  lastQuery: string;
  pagination: PaginationConfig;
  constructor() {
      this.state = 'closed';
      this.selectionItems = [];
      this.selectedRecord = null;
      this.selectedId = -1;
      this.highlightedRecord = null;
      this.highlightedId = -1;
      this.displayName = '';
      this.highlightedRowIdx = 0;
      this.lastQuery = null;
      this.pagination = {pnl_id : 2, pageSize : 25, page : 1, totalPages : 1, isLast : true};

  }

  toggleState() {
      this.state = this.state === 'closed' ? 'open' : 'closed';
  }

  setPaginationConfig(config){
    this.pagination = Object.assign(this.pagination, config);
  }
}
