import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpHeaders ,HttpErrorResponse } from '@angular/common/http';
import { HttpService } from './http.service';
const BASE_URL = window['BASE_URL'];

@Injectable({
  providedIn: 'root'
})
export class NodeServerService {

  constructor(private http:HttpClient,private httpService: HttpService) { }
  public startServer(params)
  {
  	const url:string = 'backend/node-server/start-server';
  	return this.httpService.postKeyValue(url,params);
  }	
  public stopServer(params)
  {
  	const url:string = 'backend/node-server/stop-server';
  	return this.httpService.postKeyValue(url,params);
  }	
  public getRoom(params)
  {
  	const url:string = BASE_URL+'backend/node-server/get-room';
  	return this.httpService.getKeyValue(url,params);
  }
}
