import { Component, OnInit, Input} from '@angular/core';
import { TradeHistoryService } from '../../services/trade-history.service';
declare let alertify: any;

@Component({
  selector: 'app-order-cancel-modal',
  templateUrl: './order-cancel-modal.component.html',
  styleUrls: ['./order-cancel-modal.component.css']
})
export class OrderCancelModalComponent implements OnInit {

  constructor(public tradeHistoryApi: TradeHistoryService) { }
  secretKey;
  @Input() modal:any;
  @Input() selectedRec;
  ngOnInit() {
  }

  orderCancel(secret_key_fld){
  	let me = this;
  	me.secretKey=secret_key_fld.value;
  	let params:any = {
			'secret_key' : me.secretKey,
			'market_code': me.selectedRec.code,
			'symbol'     : me.selectedRec.name,
			'order_id'   : me.selectedRec.order_id,
        };
  	me.tradeHistoryApi.cancelOrder(params).subscribe(response =>{
        if(response['success'])
        {
          alertify.success(response['message']);
        }
        else{
          alertify.error(response['message']);
        }
      	secret_key_fld.value=null;
      	me.modal.hide();
      });
  }

  orderBtnCancel(secret_key_fld){
  	secret_key_fld.value=null;
  	this.modal.hide();
  }

}
