import { Injectable } from '@angular/core';
//import { HttpClient, HttpParams, HttpHeaders ,HttpErrorResponse } from '@angular/common/http';
import { HttpService} from './http.service';
// import { Observable, of } from 'rxjs';
// declare var $;

const BASE_URL = window['BASE_URL'];

@Injectable({
  providedIn: 'root'
})
export class ClientService {

  public BASE_URL = window['BASE_URL'];
  marketNameList;
  clientSelectModel;
  mask_text:string;
	constructor(private httpService : HttpService) { }

  getEntyData(id:number,controller:string) {
    const url:string = 'backend/'+controller+'/get-entry-data';
    return this.httpService.getRequest(url,{id: id});
  }

  getClientData(id:number,controller:string) {
    const url:string = 'backend/'+controller+'/get-client-data';
    return this.httpService.getRequest(url,{id: id});
  }

  getEMarketEdit(id:number,controller:string) {
    const url:string = 'backend/'+controller+'/get-market-edit';
    return this.httpService.getRequest(url,{id: id});
  }

  getClientMarketWallet(id:number,controller:string) {
    const url:string = 'backend/'+controller+'/get-client-market-wallet';
    return this.httpService.getRequest(url,{id: id});
  }

  clientSave(body) {
    const url:string = 'backend/client/save';
    return this.httpService.postKeyValue(url,body);
  }

  getClientList(params){
    const url:string = BASE_URL+'backend/client/list';
    // return this.httpService.getRequest(url, params);
    return this.httpService.getKeyValue(url, params);
  }

  deleteClient(id) {
    const url:string = 'backend/client/delete';
    return this.httpService.postKeyValue(url,{client_id: id});
  }
  getClientMarket(params){
    const url:string = 'backend/client-market/list';
    return this.httpService.getRequest(url,params);
  }

  getMarketSelectedList(id) {
    let getId = '?id='+id;
    let url = 'backend/client-market/market-list'+getId;
    this.httpService.getRequest(url,getId).subscribe(
      res=>{
        this.marketNameList = res['data'];
      });
  }

  getMarketList(params) {
    const url:string ='backend/client-market/client-master-name-list';
    return this.httpService.getRequest(url,params);
  }

  getMarketMasterNameList(params) {
    const url:string = 'backend/client-market/market-master-name-list';
    return this.httpService.getRequest(url,params);
  }


  saveClient(body) {
   const url:string = 'backend/client-market/save-client-market';
    return this.httpService.postKeyValue(url,body);
  }

  marketWallet(params) {
    const url:string = 'backend/market-wallet/market-wallet-list';
    return this.httpService.getRequest(url,params);
  }

  clientList(params) {
   const url:string ='backend/market-wallet/client-list';
    return this.httpService.getRequest(url,params);
  }

  clientMaster(body){
    const url:string = 'backend/market-wallet/client-market-list';
    return this.httpService.postKeyValue(url,body);
  }

  saveWallet(body) {
    let url = 'backend/market-wallet/save-wallet';
    return this.httpService.postKeyValue(url,body);
  }

  saveMarket(body) {
    let url = 'backend/client-market/save-market';
    return this.httpService.postKeyValue(url,body);
  }

  saveMarketWallet(body) {
    let url = 'backend/client-market-wallet/save-market-wallet';
    return this.httpService.postKeyValue(url,body);
  }


  clientMarketWalletList(params) {
    const url:string = 'backend/client-market-wallet/list';
    return this.httpService.getRequest(url, params);
  }

  saveClientWallet(postData){
    const url:string = 'backend/client-market-wallet/save';
    return this.httpService.postKeyValue(url, postData);
  }

  toggleStatus(id:number,controllerName:string)
  {
    const url:string = 'backend/'+controllerName+'/status-toggle';
    return this.httpService.postKeyValue(url,{record_id:id});
  }

  toggleTradeStatus(id:number,controllerName:string)
  {
    const url:string = 'backend/'+controllerName+'/trade-status-toggle';//client-market-wallet
    return this.httpService.postKeyValue(url,{client_market_wallet_id:id,});
  }

  /**
   * [getCountryList return country list]
   */
  public getCountryList(){
    const url:string = 'backend/country/list';
    return this.httpService.getRequest(url,'');
  }

  public getCompanyList(params) {
    const url = BASE_URL+ 'company/getList';
    return this.httpService.getRequest(url, params);
  }

  public setClientSelectionModel(item){
    this.clientSelectModel = item;
  }

  getClientWallet(params){
    const url:string = 'backend/client-wallet/list';
    return this.httpService.getRequest(url,params);
  }

  saveCWallet(body) {
   const url:string = 'backend/client-wallet/save-client-wallet';
    return this.httpService.postKeyValue(url,body);
  }

  getCWalletEdit(id:number,controller:string) {
    const url:string = 'backend/'+controller+'/get-wallet-edit';
    return this.httpService.getRequest(url,{id: id});
  }

  saveCWalletForm(body) {
    let url = 'backend/client-wallet/save-wallet';
    return this.httpService.postKeyValue(url,body);
  }

  getClientWalletList(params:any){
    const url:string = 'backend/client-wallet/list';
    return this.httpService.getRequest(url,params);
  }
  getNetworkSettings(){
      const url:string = 'backend/network-interface/get-data';
      return this.httpService.getRequest(url,'');
  }
  saveNetworkInterface(data,id){
      let params:any = {data : data, network_interface_id : id};
      const url:string = 'network-interface/save-network-settings';
      return this.httpService.postJson(url,params);
    }

}
