import { Injectable } from '@angular/core';
import { HttpService } from './http.service';
declare var window;
const BASE_URL = window['BASE_URL'];

@Injectable({
  providedIn: 'root'
})
export class MarketService {
  public BASE_URL = window['BASE_URL']; 
  urlClient       = 'backend/market';
  urlMarketWallet = 'backend/market-wallet';

  constructor(private httpService: HttpService) { }

  /**
   * [getEntyData fetch record by id]
   * @param {number}[marketWallet id]
   * @param {string}[php controller name]
   */
  getEntyData(id:number,controller:string)
  {
    const url:string = 'backend/'+controller+'/get-entry-data?id='+ id;
    return this.httpService.getRequest(url,id);
  }

  /**
   * [getMarketWalletEdit]
   * @param {number}[marketWallet id]
   */
  getMarketWalletEdit(id:number) {
    const url:string = this.urlMarketWallet+'/get-market-wallet?id='+ id;
    return this.httpService.getRequest(url,id);
  }

  /**
   * [saveMarket]
   * @param {[FormGroup]} [form data]
   */
  saveMarket(body)
  {
    const url:string = 'backend/market/save';
    return this.httpService.postKeyValue(url,body);
  }

  /**
   * [saveWallet]
   * @param {[FormGroup]} [form data]
   */
  saveWallet(body) {
    const url:string = this.urlMarketWallet+'/save';
    return this.httpService.postKeyValue(url,body);
  }

  /**
   * [getMarketList]
   * @param {[obj]} params [pnl_id]
   */
  public getMarketList(params){
    const url:string =  BASE_URL +'backend/market/list';
    return this.httpService.getKeyValue(url, params);
  }

  /**
   * [getMarketWalletList]
   * @param {[obj]} params [market_id]
   */
  public getMarketWalletList(params){
    const url = this.urlMarketWallet+'/list';
    return this.httpService.getRequest(url, params);
  }

  /**
   * [deleteMarket]
   * @param {[number]} [market id]
   */
  deleteMarket(id)
  {
    const url:string = 'backend/market/delete';
    return this.httpService.postKeyValue(url,{market_id:id});
  }

  /**
   * [marketMaster list]
   * @param {[obj]} [pnl_id]
   */
  public marketMaster(params){
    let url = this.urlMarketWallet+'/market-list';
    return this.httpService.getRequest(url, params);
  }

  /**
   * [saveMarketWallet]
   * @param {[FormGroup]} [marketwallet form data]
   */
  saveMarketWallet(body) {
    const url = this.urlMarketWallet +'/save-market-wallet';
    return this.httpService.postKeyValue(url,body);
  }

  /**
   * [walletMaster list]
   * @param {[obj]} [pnl_id]
   */
  public walletMaster(params){
    let url = this.urlMarketWallet+'/wallet-list';
    return this.httpService.getRequest(url, params);
  }

  /**
   * [toggleStatus toggle is_locked to 1/0 for market-list & market-wallet-list]
   * @param {number} [record id]
   * @param {string} [controllerName]
   */
  toggleStatus(id:number,controllerName:string)
  {
    const url:string = 'backend/'+controllerName+'/status-toggle';
    return this.httpService.postKeyValue(url,{record_id:id});
  }
 
 /** [getCountryList] */
  public getCountryList(){
    const url:string = 'backend/country/list';
    return this.httpService.getRequest(url,'');
  } 

  public getMarketFeeList(params:any = {}):any {
    let url = this.urlClient+'/market-fee';
    return this.httpService.getRequest(url, params);
  }

  public saveMarketFeeApi(params:any): any{
    let url = BASE_URL + 'backend/market/save-market-fee';
    return this.httpService.postJson(url, params);
  }

  public verifyMarketWallet(params:any):any{
    let url = BASE_URL + 'backend/market-wallet/verify-market-wallet';
    return this.httpService.postJson(url, params); 
  }
}
