import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder ,FormControl, Validators } from '@angular/forms';
import { Router,ActivatedRoute} from '@angular/router';
import { ClientService } from '../../../services/client.service';
declare let alertify: any;

@Component({
  selector: 'app-client-wallet-form',
  templateUrl: './client-wallet-form.component.html',
  styleUrls: ['./client-wallet-form.component.css']
})
export class  ClientWalletFormComponent implements OnInit {
  private clientForm : FormGroup;
  private formErrors:any = {};
  private isNew:Boolean = true;
  selectedClient;
  selectedWallet;

  constructor(private router: Router, public clientApi: ClientService, private route:ActivatedRoute, private fb : FormBuilder) { }

  ngOnInit() {
  	this.createForm();
    if(this.route.snapshot.routeConfig.path == "new"){
      this.isNew = true;
    }
    else{
      this.isNew = false;
      if(this.route.snapshot.params.id){
        let id = this.route.snapshot.params.id;
        this.clientApi.getCWalletEdit(id,'client-wallet').subscribe(result => {
          if(result['success'] && result['data']){
            let data = result['data'];
            this.clientForm.setValue(data);
            this.selectedClient = result['client_list']['data'];
            this.selectedWallet = result['wallet_list']['data'];
          }
          else{
            this.router.navigate(['/client/walletList']);
            alertify.error('Record Not Found');
          }
        });
      }
    }
  }

  createForm() {
    this.clientForm = this.fb.group({
      id              : [''],
      code            : [''],
      client_id       : [''],
      wallet_id       : [''],
      client_name_ja  : [''],
      client_name_en  : [''],
      registration_fee: ['0', [Validators.required, Validators.min(0)]],
      status          : ['1',Validators.required],
    });
  }

  onSubmit()
  {
    let me = this;
    if(!this.clientForm.valid){
      this.formErrors = {};
      /* IF FORM IS INVALID SET ERROR MESSAGE */
      this.setFormErrorMsg();
      // this.validateAllFormFields(this.staffDetailForm);
    }
    else{
      var body = {
        'Client[id]'              : me.clientForm.value.id,
        'Client[client_id]'       : me.clientForm.value.client_id,
        'Client[wallet_id]'       : me.clientForm.value.wallet_id,
        'Client[registration_fee]': me.clientForm.value.registration_fee,
        'Client[is_locked]'       : me.clientForm.value.status ? 0 : 1 
      }
      if(!this.isNew){
        body['Client[id]'] = me.clientForm.value.id;
      }
      this.clientApi.saveCWalletForm(body).subscribe(response =>{
        if(response['success'])
        {
          this.router.navigate(['client/walletList']);
            alertify.success(response['message']);
        }
        else{
            alertify.error(response['message']);
        }
      });
    }
  }

  setFormErrorMsg(){
    let errors = this.getFormErrors(this.clientForm);

    for(let key in errors){
      if(errors[key] != null){
        let obj:any = errors[key];
        if(obj.hasOwnProperty('required')){
          this.formErrors[key] = 'This field is required.';
        }
        else if(obj.hasOwnProperty('min')){
          this.formErrors[key] = 'This field\'s value must be greater or equal to ' + obj['min']['min'] + '.';
        }
      }
    }
  }

  getFormErrors(formGroup: FormGroup){
    let errors: any = {};
    Object.keys(formGroup.controls).forEach(field => {
      const control = formGroup.get(field);
      if (control instanceof FormControl) {
        errors[field] = control.errors;
      } else if (control instanceof FormGroup) {
        errors[field] = this.getFormErrors(control);
      }
    });
    return errors;  
  }

  onCancelClick()
  {
    let me = this;
    var message = "Do you want to cancel?";
    alertify.confirm(message, function(e) {
        if(e) {
            me.router.navigate(['/client/walletList']);
        }else{
          // debugger;
        }
      })
  }

}
