import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { NodeServerService } from '../../services/node-server.service';
import * as io from 'socket.io-client';

declare var Orbit:any;
declare var alertify:any;
declare var $:any;
declare let window: any;

const SOCKET_URL: string = window['SOCKET_URL'];

@Component({
  selector: 'app-node-server',
  templateUrl: './node-server.component.html',
  styleUrls: ['./node-server.component.css']
})
export class NodeServerComponent implements OnInit, OnDestroy  {
  public socket;
  public reconnectionDelay:number = 5000;
  public rooms = [];
  public activeHostId = 1;
  public activeHostSelModel = Orbit['NODE_SERVERS']; //[{
  //   id: 1,
  //   host : '127.0.0.1',
  //   server_active : false,
  //   port : 3001
  //   },
  //   {
  //   id: 2,
  //   host : '127.0.0.1',
  //   server_active : false,
  //   port : 3002
  //   }];

  constructor(public route:Router, public nodeServerApi:NodeServerService) { }

  ngOnInit() {
  	// this.getRoom();
  	this.connect();
  }

  ngOnDestroy() {
    let me = this,
        obj:any = {
              items              : [],
              bidRateArr         : {},
              askRateArr         : {},
              base_balance_arr  : {},
              quote_balance_arr    : {},
              // cash_balance_arr   : {},
              tickers            : {},
              max_bid_market     : '',
              min_ask_market     : '',
              base_balance_total: 0,
              quote_balance_total  : 0,
              // cash_balance_total : 0,
              base_jpy_rate       : 0,
              quote_jpy_rate       : 0,
              symbol             : Orbit.CURRENT_USER.symbol,
              tradeActionChecked : true
            };
      if(this.socket){
        this.socket.io.disconnect();
      }
  }

  getRoom() {
  	this.nodeServerApi.getRoom([]).subscribe(response=>{
      if(response['success']){
        // alertify.success(response['message']);
        this.rooms = response['data'];
        this.connect();
      }
      else{
        alertify.error(response['message']);
      }
      //TODO
    });
  }
  startServer() {
    let me = this;
    this.nodeServerApi.startServer([]).subscribe(response=>{
      if(response['success']){
        if(!me.socket.connected){
          // me.socket.io.reconnect();
          me.connect();
        }
        alertify.success(response['message']);
      }
      else{
        alertify.error(response['message']);
      }
      //TODO
    });
  }
  stopServer() {
    let me = this;
    this.nodeServerApi.stopServer([]).subscribe(response=>{
      if(response['success']){
        alertify.success(response['message']);
      }
      else{
        alertify.error(response['message']);
      }
      //TODO
    });
  }
  /**
   * Initialize Socket Conneciton
   * @param {[FormGroup]} bitForm [BitForm]
   */
  	connect(){
  		let me = this;
  	  let user:any = Orbit.CURRENT_USER;
  		this.socket = io(SOCKET_URL, {
  			reconnection: true,
  			reconnectionDelay: me.reconnectionDelay,
  			// reconnectionDelayMax : 7000,
  			query: 'user_id='+user.user_id+'&access_token='+user.access_token
      });
      this.socket.on('server_status', function(resp){
        Object.keys(me.activeHostSelModel).forEach(function(key) {
          if(me.activeHostSelModel[key]['id'] == resp.id){
            me.activeHostSelModel[key]['server_active'] = resp.status == 'Active' ? true : false;
          }
        });
      });
      this.socket.on('admin_log', function(result){
        let  data = result.data,
          host_id = result.host_id,
          log_type = me.getAlertType(data), //data.includes("[ERROR]") ? 'error' : 'info',
          status_color = log_type == 'error' ? 'red' : 'green';
        let  element = <HTMLInputElement>document.getElementById(host_id);
        if(element) {
          data = '<div class="alert-'+log_type+'" >'+data+'</div>';
          let item = document.createElement('div');
          item.className = "log-div";
          item.innerHTML = data;
          // let text = element.innerHTML;
          // text = data.concat(text);
          element.insertBefore(item, element.firstChild);
          // element.innerHTML = text;
        }
      });
      this.socket.on('disconnect', function(data) {
        Object.keys(me.activeHostSelModel).forEach(function(key) {
           me.activeHostSelModel[key]['server_active'] = false;
        });
      });
    }
    onHostChange(serverHostItem) {
      this.activeHostId = serverHostItem['id'];
    }
    getAlertType(data){
      if (data.includes("[ERROR]")){ return 'danger';}
      else if(data.includes("[INFO]")){ return 'info';}
      else if(data.includes("[WARNING]")){ return 'warning';}
      else if(data.includes("[ALERT]")){ return 'warning';}
      else if(data.includes("[SUCCESS]")){ return 'success';}
      else{ return 'info'};

    }
}
