import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpHeaders ,HttpErrorResponse } from '@angular/common/http';
import { HttpService } from './http.service';
const BASE_URL = window['BASE_URL'];

@Injectable({
  providedIn: 'root'
})
export class TradeHistoryService {
  public BASE_URL = window['BASE_URL'];
  public orderDetailData:any = {};
  public isOrderDetailDataEmpty:boolean = true;

  constructor(private http:HttpClient,private httpService: HttpService) { }
  
  public getTradeHistoryList(params)
  {
  	const url:string = BASE_URL+'backend/trade-history/list';
  	return this.httpService.getKeyValue(url,params);
  }

  public viewOrderDetail(recId:number,orderId:string,marketCode:string){
		let params:any = {id : recId, order_id : orderId, market_code : marketCode};
		const url = 'backend/trade-history/fetch-order';
		return this.httpService.getRequest(url, params);
	}
	
	public cancelOrder(params)
  {
  	const url:string ='backend/trade-history/order-cancel';
  	return this.httpService.postKeyValue(url,params);
  }	
}
