import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder , FormArray, FormControl, Validators } from '@angular/forms';
import { Router, ActivatedRoute} from '@angular/router';
import { MarketService } from '../../../services/market.service';
declare let alertify: any;

@Component({
  selector   : 'app-market-wallet-form',
  templateUrl: './market-wallet-form.component.html',
  styleUrls  : ['./market-wallet-form.component.css']
})
export class MarketWalletFormComponent implements OnInit {

  private marketForm:FormGroup;
  private isNew     :Boolean = true;
  private formErrors:any = {};
  public selectedWallet;
  public selectedMarket;

  constructor(private router: Router, private route:ActivatedRoute, public marketApi:MarketService, private fb : FormBuilder) { }

  ngOnInit() {
  	this.createForm();
    if(this.route.snapshot.routeConfig.path == "new"){
      this.isNew = true;
    }
    else{
      this.isNew = false;
      if(this.route.snapshot.params.id){
        let id = this.route.snapshot.params.id;
        let me = this;
        this.marketApi.getMarketWalletEdit(id).subscribe(result => {
          if(result['success'] && result['data']){
            let data = result['data'];
            me.marketForm.setValue(data);
            this.selectedWallet = result['wallet_list'];
            this.selectedMarket = result['market_list']['data'];
          }
          else{
            me.router.navigate(['/market/walletList']);
            alertify.error('Record Not Found');
          }
        });
      }
    }
  }

  createForm(){
    this.marketForm = this.fb.group({
      code              : ['',Validators.required],
      id                : [''],
      market_id         : ['',Validators.required],
      market_name_en    : ['',Validators.required],
      market_name_ja    : ['',Validators.required],
      registration_fee  : ['',[Validators.required,Validators.min(0), Validators.pattern( /[0-9\.]/)]],
      status            : ['1',Validators.required],
      wallet_id         : ['',Validators.required],
     });
  }

  onSubmit()
  {
    let me = this;
    if(!this.marketForm.valid){
      this.formErrors = {};
      /* IF FORM IS INVALID SET ERROR MESSAGE */
      this.setFormErrorMsg();
      // this.validateAllFormFields(this.staffDetailForm);
    }
    else{
      var body = {
        'Wallet[code]'            :me.marketForm.value.code,
        'Wallet[market_name_en]'  :me.marketForm.value.market_name_en,
        'Wallet[market_name_ja]'  :me.marketForm.value.market_name_ja,
        'Wallet[registration_fee]':me.marketForm.value.registration_fee,
        'Wallet[wallet_id]'       :me.marketForm.value.wallet_id,
        'Wallet[market_id]'       :me.marketForm.value.market_id,
        'Wallet[is_locked]'       :me.marketForm.value.status ? 0 : 1 
      }
      if(!this.isNew){
        body['Wallet[id]'] = me.marketForm.value.id;
      }
      this.marketApi.saveWallet(body).subscribe(response =>{
        if(response['success'])
        {
          this.router.navigate(['market/walletList']);
            alertify.success(response['message']);
        }
        else{
            alertify.error(response['message']);
        }
      });
    }
  }

  setFormErrorMsg(){
    let errors = this.getFormErrors(this.marketForm);

    for(let key in errors){
      if(errors[key] != null){
        let obj:any = errors[key];
        if(obj.hasOwnProperty('required')){
          this.formErrors[key] = 'This field is required.';
        }
        else if(obj.hasOwnProperty('min')){
          this.formErrors[key] = 'This field\'s value must be greater than ' + obj['min']['min'] + '.';
        }
        else if(obj.hasOwnProperty('pattern')){
          this.formErrors[key] = 'Invalid input data.';
        }
        else{
        }
      }
    }
  }

  /**
   * @author ROHAN :: 2018-05-30
   * Fetch all form errors
   * @param {FormGroup} formGroup [Staff Detail Form]
   * @returns {[Object]} errors
   */
  getFormErrors(formGroup: FormGroup){
    let errors: any = {};
    Object.keys(formGroup.controls).forEach(field => {
      const control = formGroup.get(field);
      if (control instanceof FormControl) {
        errors[field] = control.errors;
      } else if (control instanceof FormGroup) {
        errors[field] = this.getFormErrors(control);
      }
    });
    return errors;  
  }

  onCancelClick(){
    let me = this;
    var message = "Do you want to cancel?";
    alertify.confirm(message, function(e) {
      if(e) {
          me.router.navigate(['/market/walletList']);
      }else{
        // debugger;
      }
    });
  }

}
