import {  AfterViewInit, Component, OnInit, ViewChild } from '@angular/core';
import { ClientService } from '../../../services/client.service';
import { MarketService } from '../../../services/market.service';
import { WalletService } from '../../../services/wallet.service';
import DataTables from 'datatables.net-dt';
import { DataTableDirective } from 'angular-datatables';
import { PAGE_PER_SIZE} from '../../../config/constant';
import { Router, ActivatedRoute } from '@angular/router';
declare let alertify: any;
declare let Orbit : any;
declare var $: any;

@Component({
	selector: 'app-client-list',
	templateUrl: './client-list.component.html',
	styleUrls: ['./client-list.component.css']
})
export class ClientListComponent implements OnInit, AfterViewInit {
	clientList = [];
  	marketList = [];
	walletList = [];

	public sColumn:any = null;
	public activeRowIdx:number     = 0;
	public mask_txt : string = "Loading...";
  	public showLoadingGIf: boolean = true;
  	public selectedClient;
  	public client_status = Orbit['CLIENT_LOCKED_STATUS'];

	@ViewChild(DataTableDirective)
	public datatableElement: DataTables;

	public dtOptions: DataTables.Settings = {};
	public cols = [
		{ title:'User Name', data: 'username'},
		{ title:'Client Type', data: 'client_type' },
		{ title:'Client Name EN', data: 'client_name_en' },
		{ title:'Client Name JA', data: 'client_name_ja' },
		{ title:'Status', data: 'is_locked', sortable:false  },
		{ title:'Trade Count', data:'trade_counter' },
		{ title:'Action', data:'id', sortable:false }
	];

	constructor(private router:Router, private route:ActivatedRoute,
				public clientApi: ClientService) { }

	ngOnInit() {

		
		// console.log(this.route.snapshot.routeConfig.path);
		var me =this;
		// this.getClientList();
		this.dtOptions = {
			pagingType: 'simple_numbers',
			pageLength: PAGE_PER_SIZE,
			serverSide: true,
			processing: true,
			bLengthChange: true,
			aaSorting: [[ 3, "desc" ]],
			lengthMenu: [ [10, 25, 50,100,500], [10, 25, 50,100,500] ],
			language: {
			  "zeroRecords": " ",
			  "search":" ",
			  "emptyTable": "No records found",
        	  "lengthMenu": "Page Size _MENU_ "
			},
			dom: 'rt<"clear"><"bottom"lip><"clear">',
			ajax: (tablesParameters: any, callback) => {
			  tablesParameters.pnl_id = 1;
				$('.filter-input').on('keyup change', function (e) {
		            me.sColumn = e.target.name;
		        });
		        tablesParameters['sColumn'] = this.sColumn;
		        this.activeRowIdx = 0;
			 	this.clientApi.getClientList(tablesParameters).subscribe(res => {
				this.clientList = res.data;
				if(res.data.length !=0){
					this.selectedClient = res.data[0].id;
				}else{
					this.selectedClient = 0;
				}
				this.showLoadingGIf = true;
				this.getClientWallet();
				this.getClientMarket();
				callback({
					recordsTotal: res.recordsTotal,
					recordsFiltered: res.recordsFiltered,
					data: []
				 });
				 $('#client-table tfoot tr').appendTo('#client-table thead');
			  })
			},
			columns:this.cols
		};
	}

	ngAfterViewInit(): void {
	    var me = this;
	    this.datatablesFilterProcessing();

	}

	getClientWallet(){
		this.showLoadingGIf = true;
		let params:any = {
	      client_id  : this.selectedClient,
	      pnl_id     : 1
	    }
		this.clientApi.getClientWallet(params).subscribe(response => {
			this.walletList = response['data'];
			this.showLoadingGIf = false;
		});	
	}

	getClientMarket(){
		this.showLoadingGIf = true;
		let params:any = {
	      client_id  : this.selectedClient,
	      pnl_id     : 1
	    }
		this.clientApi.getClientMarket(params).subscribe(response => {
			this.marketList = response['data'];
			this.showLoadingGIf = false;
		});
	}

	datatablesFilterProcessing(){
		var me =this;
		this.datatableElement.dtInstance.then((dtInstance: DataTables.Api) => {
	      dtInstance.columns().every(function () {
	        const that = this;
	        $('input', this.footer()).on('keyup', function (e) {
	          if(e.keyCode == 13){
	            if (that.search() !== this['value']) {
	              // me.search['type'] = me.sColumn == 'trade_base_amount' || me.sColumn == 'spred_percent'? 'num' : null;
		            that
	              	.search(this['value'])
	              	.draw();
	            }
	          }else if(e.keyCode == 8 || e.keyCode == 48){
	            if((this['value']) == ''){
	              //reload datatable
	              dtInstance
	                .cell(that)
	                .search('')
	                .draw();
	            }
	          }
	        });

	        $('.is_locked', this.footer()).on('change', function (e) {
            if (that.search() !== this['value']) {
              // me.search['type'] = me.sColumn == 'trade_base_amount' || me.sColumn == 'spred_percent'? 'num' : null;
	            that
              	.search(this['value'])
              	.draw();
            }
	          // if(e.keyCode == 13){
	          // }else if(e.keyCode == 8 || e.keyCode == 48){
	          //   if((this['value']) == ''){
	          //     //reload datatable
	          //     dtInstance
	          //       .cell(that)
	          //       .search('')
	          //       .draw();
	          //   }
	          // }
	        });
	      });
	    });
	 }

	onDatatableReload(){
      // me.sColumn = $(this).attr('name');
      $('#client-table').DataTable().draw();
  	}

	getClientList() {
		let params:any = {pnl_id : 1};
		this.clientApi.getClientList(params).subscribe(res=>{
			this.clientList=res["data"]
		});
	}

	edit(id) {
		this.router.navigate([`client/edit/${id}`]);
	}
	walletlist(id){
		this.router.navigate([`client/walletList/${id}`]);
	}
	marketlist(id){
		this.router.navigate([`client/marketList/${id}`]);
	}
	marketWalletlist(id){
		this.router.navigate([`client/marketWalletList/${id}`]);
	}

	delete(id) {
		let me = this;
	    var message = "Do you want to delete this record?";
	    alertify.confirm(message, function(e) {
	      	if(e) {
	      		me.clientApi.deleteClient(id).subscribe(response =>{
	      			if(response['success'] == true){
						  // me.getClientList();
						  me.reloadDataTable();
	              		alertify.success(response['message']);
	            	}else{
	            		alertify.error(response['message']);
	            	}
	        	});
	      	}else{
	        // debugger;
	      }
	    })
  	}

	onClientSelect(id,indx) {
		this.selectedClient = id;
		this.activeRowIdx = indx;
		this.showLoadingGIf = true;
		this.getClientWallet();
		this.getClientMarket()
	}

	onOffClientStatusChange(id) {
		let controllerName = 'client';
		let me = this;
    	this.clientApi.toggleStatus(id,controllerName).subscribe(res=>{
			if(res['success']==true) {
				alertify.success(res['message']);
				// this.getClientWallet();
				// this.getClientMarket();
				//me.reloadDataTable();
			} else {
				alertify.error(res['message']);
	    		// this.getClientList();
			}
    	});
	}
	onOffClientWalletStatusChange(id)
  	{
  		let controllerName = 'client-wallet';
	    this.clientApi.toggleStatus(id,controllerName).subscribe(response=>{
	      if(response['success']==true)
	      {
	        alertify.success(response['message']);
	      }
	      else{
	        alertify.error(response['message']);
	      }
	    });
  	}

  	onOffClientMarketStatusChange(id)
  	{
	    let controllerName = 'client-market';
	    this.clientApi.toggleStatus(id,controllerName).subscribe(res=>{
	      if(res['success']==true)
	      {
	        alertify.success(res['message']);

	      }
	      else{
	        alertify.error(res['message']);
	      }
	    });
  	}

	public reloadDataTable(){
		this.datatableElement.dtInstance.then((dtInstance: DataTables.Api) => {
			dtInstance.draw();
		});
	}
} // CLOSE COMPONENT
