import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';

import { MarketListComponent } from './market-list/market-list.component';
import { MarketFormComponent } from './market-form/market-form.component';
import { MarketWalletFormComponent } from './market-wallet-form/market-wallet-form.component';
import { MarketWalletListComponent } from './market-wallet-list/market-wallet-list.component';
import { MarketWalletAssignComponent } from './market-wallet-assign/market-wallet-assign.component';
import { MarketFeeComponent } from './market-fee/market-fee.component';

const routes: Routes = [
	{ path : '', redirectTo: 'list', pathMatch : 'full' },
	{ path : 'list', component : MarketListComponent, pathMatch : 'prefix' },
	{ path : 'edit/:id', component : MarketFormComponent, pathMatch : 'prefix' },
	{ path : 'delete/:id', component : MarketListComponent, pathMatch : 'prefix' },
	{ path : 'new', component : MarketFormComponent, pathMatch : 'prefix' },
	{ path : 'walletList', component : MarketWalletListComponent, pathMatch : 'prefix' },
	{ path : 'walletList/edit/:id', component : MarketWalletFormComponent, pathMatch : 'prefix' },
	{ path : 'walletList/delete/:id', component : MarketWalletListComponent, pathMatch : 'prefix' },
	{ path : 'walletAssign', component : MarketWalletAssignComponent, pathMatch : 'prefix' },
	{ path : 'market/marketFee', component : MarketFeeComponent, pathMatch : 'prefix' },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MarketRoutingModule { }
