import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder , FormArray, FormControl, Validators } from '@angular/forms';
import { ClientService } from '../../services/client.service';
declare let alertify: any;
declare let Orbit: any;
import * as io from 'socket.io-client';
const SOCKET_URL: string = window['SOCKET_URL'];

@Component({
  selector: 'app-network-settings',
  templateUrl: './network-settings.component.html',
  styleUrls: ['./network-settings.component.css']
})
export class NetworkSettingsComponent implements OnInit {

  public form: FormGroup;
  public networkIdForm: FormGroup;
  public networkData: Array<any>;
  public val = 0;
  public socket;
  public reconnectionDelay:number = 5000;
  constructor( public fb: FormBuilder, public clientApi : ClientService ) { }

  ngOnInit() {
  	this.createForm();
  	this.createNetworkIdForm();
  	this.getNetworkInterface();
    this.connect();
  }

  connect(){
      let me = this;
      let user:any = Orbit.CURRENT_USER;
      this.socket = io(SOCKET_URL, {
        reconnection: true,
        reconnectionDelay: me.reconnectionDelay,
        // reconnectionDelayMax : 7000,
        query: 'user_id='+user.user_id+'&access_token='+user.access_token
    });
  }

  getNetworkInterface(){
  	var me = this;
  	this.clientApi.getNetworkSettings().subscribe(res => {
      // if(res['success']){
      	this.networkData = res['data'];
      	this.networkIdForm.controls.network_interface_id.setValue(res['network_interface_id']);
      	this.removeKeyItem(0);
      	this.networkData.forEach(function(item){
        const control = <FormArray>me.form.controls['ipKeys'];
        control.push(me.createKeyItems(item));
      });
      	// this.form.controls['apache_network_interface'].setValue(res['data']);
      // }
    });
  }

  createForm(){
  	 this.form = this.fb.group({
        ipKeys : this.fb.array([ this.createKeyItems()]),
     });
  }

  createNetworkIdForm(){
  	 this.networkIdForm = this.fb.group({
  	 	'network_interface_id' : ['']
  	 });
  }

  createKeyItems(data:any = {}) : FormGroup {
  	const ipPattern = 
    "(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)";
    return this.fb.group({
      network_id        : [{value: data.network_id || 'eth'+ this.val, disabled: false}/*,Validators.required]*/],
      publicIp          : [data.publicIp, [Validators.pattern(ipPattern)/*,Validators.required]*/ ]],
      privateIp         : [data.privateIp, [Validators.pattern(ipPattern)/*,Validators.required]*/ ]],
    });
  }

  addNewRow() {
    // control refers to your formarray
    const control = <FormArray>this.form.controls['ipKeys'];
    let keyLength = this.form.value.ipKeys['length'] ,
        lastVal =  this.form.value.ipKeys[keyLength-1].network_id,
        // lastNetworkId = lastVal.slice(0,3) + lastVal.slice(-1);
        lastIndx = JSON.parse(lastVal.slice(-1));
    this.val = lastIndx + 1;
    // add new formgroup
    // this.val = this.form.controls.ipKeys['length'] + 1; //5
    control.push(this.createKeyItems());
  }

  deleteRow(index) {
    this.val = this.form.controls.ipKeys['length']-1;
    // control refers to your formarray
    const control = <FormArray>this.form.controls['ipKeys'];
     alertify.confirm("Are you sure ? ", function(e){
        if(e){
          // remove the chosen row
          control.removeAt(index);
        }
      });
  }

  removeKeyItem(idx:number){
    const control = <FormArray>this.form.controls['ipKeys'];
    control.removeAt(idx);
  }

  onSubmit(){
  	if(this.form.valid){
	  	var me = this;
	  	let formValue:Array<any> = this.form.value.ipKeys;
	    let postData = [];
		formValue.forEach(function(item){
			postData.push({
	            network_id : item.network_id,
	            privateIp  : item.privateIp,
	            publicIp   : item.publicIp,
	        });
		});
    let ipStrList  = "[{";

    formValue.forEach(function(element) {
     ipStrList += 
              '"'+element['network_id']+'":{'+ 
                '"privateIp":'+'"'+element['privateIp']+'",'+
                '"publicIp":'+'"'+element['publicIp']+'"},'
              ;
    });
    ipStrList += "}]";
    let content = ipStrList.slice(0,ipStrList.lastIndexOf(',')) + ipStrList.slice(ipStrList.lastIndexOf(',')+1,ipStrList.length)
	  let network_interface_id = this.networkIdForm.value.network_interface_id;
	  let msg = "Do you want to save ?";

	    alertify.confirm(msg, function(e){
	    	if(e){
				me.clientApi.saveNetworkInterface(postData,network_interface_id).subscribe(res => {
          let data = {
            'list' : content,
            'id'   : network_interface_id
          }
          if(res['success']){
						alertify.success(res['message']);
             me.socket.emit('updateNetworkInterfaceConfig',data);
					}else{
						alertify.error(res['message']);
					}
				});
	    	}
	    });
  	}else{
  		alertify.error('Enter valid IP Address');
  	}

  }

  onReset(){
    for (var i = 0; i < this.form.controls.ipKeys['length']; ++i) {
      this.form.controls.ipKeys['controls'][i].controls.privateIp.setValue("")
      this.form.controls.ipKeys['controls'][i].controls.publicIp.setValue("");
    }
    this.networkIdForm.controls.network_interface_id.setValue('');
  }

}
