import { Component, OnInit } from '@angular/core';
import { HomeService } from '../../services/home.service';
// import { NgDatepickerModule  } from 'ng2-datepicker';

declare var $:any;
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
    fromDate;
    toDate;
    totalRecord = {};
    date:Date;
    changeDateStatus;
    // options: NgDatepickerModule  = {
    //     minYear: 1970,
    //     maxYear: 2030,
    //     displayFormat: 'YYYY-MM-DD',
    //     // barTitleFormat: 'MMMM YYYY',
    //     // dayNamesFormat: 'dd',
    //     firstCalendarDay: 0, // 0 - Sunday, 1 - Monday
    //   };
  constructor(public apiHome:HomeService) { }
  ngOnInit() {
    var me = this;
    this.date = new Date();
    this.getTotalRecord();
    let date        = new Date(), y = date.getFullYear(), m = date.getMonth();
    let getFirstDay = new Date(y, m, 1);
    this.fromDate   = new Date(getFirstDay);
    this.toDate = new Date();
    $(document).ready(function() {
        me.datePickerConfig();
        me.setChangedDate();
    });

  }

  datePickerConfig(){
    var me = this;
      $( ".datepicker" ).datepicker({
        dateFormat: "yy-mm-dd",
        showOn: "none",
        showAnim: 'slideDown',
        buttonImageOnly: true,
        showButtonPanel: true ,
        autoSize: true,
        closeText: "Clear",
        maxDate: new Date(),
      });

      //show datepicker on click
      $('#dateFrom').click(function(){
          $('#dateFrom').datepicker('show');
      });
      $('#dateTo').click(function(){
          $('#dateTo').datepicker('show');
      });

      //filter on date change
      $('#dateFrom').on( 'change', function (e) {
          me.onFromMonthChange(e)
      })
      $('#dateTo').on( 'change', function (e) {
          me.onToMonthChange(e)
      })


  }

  setChangedDate(){
    $("#dateFrom").datepicker("setDate", this.formatDate(this.fromDate));
    $("#dateTo").datepicker("setDate", this.formatDate(this.toDate));
  }

  onFromMonthChange(e){
    let event = new Date(e.target.value);
    this.changeDateStatus = 'from';
    this.changeDate(event);
    this.fromDate = this.formatDate(event);//event+'-01';
    this.renderChart();
  }
  onToMonthChange(e){
    let event = new Date(e.target.value);
    this.changeDateStatus = 'to';
    this.changeDate(event);
    this.toDate = this.formatDate(event);
    this.renderChart();
  }

  public changeDate(argDate) {
    let todayDate = new Date(), dtFrom: Date, dtTo:Date, getFirstDay:Date, getLastDay:Date,year , month;
    dtFrom = new Date(this.fromDate);
    dtTo = new Date(argDate);
    year = dtTo.getFullYear();
    month = dtTo.getMonth();

    if(this.changeDateStatus == 'from') {
      dtFrom = new Date(argDate);
      dtTo = new Date(this.toDate);
      year = dtFrom.getFullYear();
      month = dtFrom.getMonth();
    }
    getFirstDay = new Date(dtFrom);
    getLastDay  = new Date(dtTo);

    if(dtFrom.getTime() > dtTo.getTime()){
      getFirstDay = new Date(year, month, 1);
      if(this.changeDateStatus == 'from' && dtFrom.getTime() > dtTo.getTime()) {
        getFirstDay = dtFrom;
        getLastDay  = new Date(year, month + 1, 0);
      }
      if(getLastDay.getTime() > todayDate.getTime()) {
        getLastDay  = new Date();
      }
    }
    this.fromDate = new Date(getFirstDay);
    this.toDate = new Date(getLastDay);
    this.setChangedDate();
  }

  ngAfterViewInit() {
    this.renderChart();
  }

  public renderChart(){
    let chartConfig = {
      chart: {
          type: 'column'
      },
      title: {
          text: ''
      },
      xAxis: {
          categories: []
      },
      yAxis: {
          min: 0,
          title: {
              text: 'Total Trade History'
          },
          stackLabels: {
              enabled: true,
              style: {
                  fontWeight: 'bold',
                  color: 'gray'
              }
          }
      },
      legend: {
          align: 'right',
          x: -30,
          verticalAlign: 'top',
          y: 25,
          floating: true,
          backgroundColor: 'white',
          borderColor: '#CCC',
          borderWidth: 1,
          shadow: false
      },
      tooltip: {
          headerFormat: '<b>{point.x}</b><br/>',
          pointFormat: '{series.name}: {point.y}<br/>Total: {point.stackTotal}'
      },
      plotOptions: {
          column: {
              stacking: 'normal',
              dataLabels: {
                  enabled: true,
                  color: 'white'
              }
          }
      },
      credits: {
        enabled: false
      },
      series: []
  };

  	let params = {
		current_date : this.formatDate(this.fromDate),//'2018-01-31'
    end_date : this.formatDate(this.toDate)
    }

	 this.apiHome.stackedChart(params).subscribe(res=>{
         let series = res.series;
         let categories = []

         Object.keys(res.categories).forEach(element=>{
            categories.push(element);
         });
         
    //   chartConfig.series = [
    //      {
    //       name: 'Buy Success',
    //       data: [5, 3, 4, 7, 4, 2, 1] 
    //     },{
    //       name: 'Buy Failed',
    //       data: [2, 2, 3, 2, 18, 3, 9]
    //     },{
    //       name: 'Sell Success',
    //       data: [3, 4, 4, 2, 22, 32, 6]
	// 	  },{
	// 		name: 'Sell Failed',
	// 		data: [3, 4, 4, 2,33, 5, 12]
	// 	 }
    //   ];
        chartConfig.series = series;
        chartConfig.xAxis.categories =categories;

    
		// chartConfig.xAxis.categories = ['Buy_Success', 'Buy_Failed', 'Sell_Success', 'Sell_Failed'];
		// chartConfig.xAxis.categories = ['Aug 1', 'Aug 10', 'Aug 13', 'Aug 14', 'Aug 16', 'Aug 17', 'Aug 21'];
      $('#container').highcharts(chartConfig);
    })

  }

  public formatDate(date) {
    var d = new Date(date),
        month = '' + (d.getMonth() + 1),
        day = '' + d.getDate(),
        year = d.getFullYear();
        month = month.length < 2 ? ('0' + month) : month;
        day = day.length < 2 ? ('0' + day) : day;
        return [year, month, day].join('-');
    }

    public getTotalRecord() {
        this.apiHome.totalRecord().subscribe(res=>{
            let recored = res.data;
            let obj = {
                totalWallet:recored.total_wallet,
                totalMarket:recored.total_market,
                totalClient:recored.total_client,
                totalTransaction:recored.total_transaction,
                totalBuy:recored.total_buy,
                totalSell:recored.total_sell
            }
            this.totalRecord=  obj;
        })
    }
}
