import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder , FormArray, FormControl, Validators } from '@angular/forms';
import { MarketService } from '../../../services/market.service';
import { Router } from '@angular/router';
import { AlertMessages } from '../../../config/lang';
declare let alertify: any;

@Component({
  selector: 'app-market-fee',
  templateUrl: './market-fee.component.html',
  styleUrls: ['./market-fee.component.css']
})
export class MarketFeeComponent implements OnInit {

  public marketFeeData: Array<any>;
  public marketFeeForm: FormGroup;
  constructor(public router:Router, public marketApi:MarketService , public fb : FormBuilder) { }

  ngOnInit() {
  	this.createForm();
  	this.getMarketFeeList();
  }

  createForm(){
     this.marketFeeForm = this.fb.group({
        marketFee : this.fb.array([ this.createKeyItems()]),
     });
  }

   createKeyItems(data:any = {}) : FormGroup {
    return this.fb.group({
		id       : data.id || '', 
		upsert	 : 0,
		maker_fee: data.maker_fee||'',
		taker_fee: data.taker_fee||'', 
    });
  }

  getMarketFeeList(){
  	let me = this;
    this.marketApi.getMarketFeeList().subscribe(res=>{
      this.marketFeeData=res.data;
      this.removeKeyItem(0);
      this.marketFeeData.forEach(function(item){
      	const control = <FormArray>me.marketFeeForm.controls['marketFee'];
      	control.push(me.createKeyItems(item));
      });
    }); 
  }

  removeKeyItem(idx:number){
    // remove marketFee from the list
    const control = <FormArray>this.marketFeeForm.controls['marketFee'];
    control.removeAt(idx);
  }

  get marketFeeFormData() {
  	return <FormArray>this.marketFeeForm.get('marketFee');
  }

  onMakerFeeChange(value,idx){
  	const control = <FormArray>this.marketFeeForm.controls['marketFee'],
    subControl:any = control.controls[idx];
    
    if(value.length > 0)//maker_fee has value
    {
    	if(this.marketFeeData[idx]['taker_fee']>0)//taker_fee empty only set validator
    	{
    		subControl.controls['upsert'].setValue(1);
    	}
    	else
    	{
      	subControl.controls['taker_fee'].setValidators([Validators.required]);
      	subControl.controls['upsert'].setValue(1);
    	}
    }
    else
    {
      subControl.controls['taker_fee'].clearValidators();
      subControl.controls['upsert'].setValue(0);
    }
    subControl.controls['taker_fee'].updateValueAndValidity();
  }

  onTakerFeeChange(value,idx){
  	const control = <FormArray>this.marketFeeForm.controls['marketFee'],
    subControl:any = control.controls[idx];
    
    if(value.length > 0)//maker_fee has value
    {
    	if(this.marketFeeData[idx]['maker_fee']>0)//maker_fee empty only set validator
    	{
    		subControl.controls['upsert'].setValue(1);
    	}
    	else
    	{
	      subControl.controls['maker_fee'].setValidators([Validators.required]);
	      subControl.controls['upsert'].setValue(1);
    	}
    }
    else
    {
      subControl.controls['maker_fee'].clearValidators();
      subControl.controls['upsert'].setValue(0);
    }
    subControl.controls['maker_fee'].updateValueAndValidity();
  }

  onSubmit()
  {
    let me =this;
    if(this.marketFeeForm.valid)
    {
      let formValue:Array<any> = this.marketFeeForm.value.marketFee;
      let postData = [];
      formValue.forEach(function(item)
      {
      	if(item.upsert === 1)
        {
          postData.push({
						id       : item.id,
						maker_fee: item.maker_fee,
						taker_fee: item.taker_fee
          });
        }
      });
      if(postData.length>0)
      {
      	for (var i = 0; i < postData.length; ++i) 
      	{
	      	if(postData[i]['maker_fee']<0 || postData[i]['taker_fee']<0)
	      	{
	      		alertify.error(AlertMessages.negativeFee);
	      		return;
	      	}
          else if(postData[i]['maker_fee']>100 || postData[i]['taker_fee']>100)
          {
            alertify.error(AlertMessages.maxFee);
            return;
          }
      	}
        me.marketApi.saveMarketFeeApi(postData).subscribe(response => {
          if(response.success)
          {
            alertify.success(response['msg']);
            me.marketFeeData = [];
            me.createForm();
            me.getMarketFeeList();
          }
          else
          {
            alertify.error(response['msg']);
          }
        });
      }
    }
  }

}
