import { Component, OnInit, Input, Output, EventEmitter, ElementRef, HostListener, ViewChildren, ViewChild, Renderer } from '@angular/core';
import { ClientService } from '../../../services/client.service';
import { WalletService } from '../../../services/wallet.service';
import { MarketService } from '../../../services/market.service';
import { ClientSelectionItem, ClientPicker } from '../../../models/ClientCombo';
import { Router } from '@angular/router';
declare var alertify:any;
declare var $:any;

@Component({
  selector: 'app-client-market-wallet-list',
  templateUrl: './client-market-wallet-list.component.html',
  styleUrls: ['./client-market-wallet-list.component.css']
})
export class ClientMarketWalletListComponent implements OnInit
{
  cliMarketWalletList:Array<any> = [];
  marketWalletList:Array<any> = [];
  clientList:Array<any>       = [];
  clientMarketList:Array<any> = [];
  walletList:Array<any> = [];
  @Input() model: ClientSelectionItem;
  mask_txt : string = "Loading...";
  showLoadingGIf: boolean = false;
  showWalletLoadingGif: boolean = false;
  activeRowIdx:number     = 0;
  selectedWallet:any = {};
  selectedClient:any = {};
  public clientInfo:ClientSelectionItem = {id: 0, client_code : null, client_name_en: null, client_name_ja: 'Select Client'};

  constructor(private route:Router,
              public walletApi:WalletService,
              public clientApi:ClientService,
              public marketApi : MarketService, private _elementRef: ElementRef) { }

  ngOnInit() {
    // this.clientApi.mask_text = "Loading...";
    // this.getClientMarketWalletList();
    // this.getWalletList();
    // this.getMarketWalletList();
    // this.getClientList();

    // this.getClientList();


    // this.getClientMarket();
  }

  /* Fetch Wallet Master List */
  getWalletList()
  {
    let client_id = this.clientApi.clientSelectModel.id || 0;
    let params:any = {'pnl_id' : 1, client_id : client_id};
    this.clientApi.getClientWalletList(params).subscribe(res=>{
      this.walletList     = res["data"];
      this.selectedWallet = res['data'].length == 0 ? {} : res['data'][0];
      this.showWalletLoadingGif = false;
      this.getClientMarketList(this.marketParams());
      this.getClientMarketWalletList(this.marketWalletParams());
      // console.log(this.clientApi.clientSelectModel);
      // this.selectedClient = this.clientList[record];
    });
  }

  /* Fetch Client Master List */
  getClientList() {
    let params:any = {'pnl_id' : 2};
    this.clientApi.getClientList(params).subscribe(res=>{
      this.clientList=res["data"];
    });
  }

  getClientMarketList(params) {
    this.showLoadingGIf = true;
    this.clientApi.getClientMarket(params).subscribe(res=>{
      this.clientMarketList = res['data'];
      this.showLoadingGIf = false;
    })
    // this.getClientMarketWalletList(this.marketParams());
  }

  getClientMarketWalletList(params) {
    // let params:any = {
    //   wallet_id : this.selectedWallet['id'],
    //   client_id : this.selectedClient.hasOwnProperty('id') ? this.selectedClient['id'] : 0
    // };
    this.showLoadingGIf = true;
  	this.clientApi.clientMarketWalletList(params).subscribe(res=>{
      this.cliMarketWalletList = res["data"];
      this.clientMarketList['count'] = res['data'].length;
      this.showLoadingGIf = false;
    });
  }

  deleteItem(idx) {
    alertify.confirm('Do you want to delete this record?', ()=> {
      this.cliMarketWalletList[idx]['is_deleted']=1;
    })
  }

  delete(id) {
    this.route.navigate([`client/marketWallet/delete/${id}`]);
  }

  onClientWalletSelectionChange(item,indx) {
    if(this.activeRowIdx != indx){
      this.showLoadingGIf = true;
      this.selectedWallet = item;
      this.activeRowIdx = indx;
      this.getClientMarketList(this.marketParams());
      this.getClientMarketWalletList(this.marketWalletParams());
    }
  }

  onClientSelect(idx) {
    this.selectedClient = this.clientList[idx];
    this.getClientMarketList(this.marketParams());
  }



  moveMarketWallet(item) {
    let obj = {
      client_id: this.clientApi.clientSelectModel.id,
      client_name_en: this.clientApi.clientSelectModel.client_name_en,
      market_id: item.market_id,
      code: item.code,
      wallet_id: this.selectedWallet.wallet_id,
      market_name_en:item.market_name_en,
      symbol: this.selectedWallet.code,
      country:item.country,
      image: 'markets/'+item.code+'.png',
      is_new : 1,
      is_deleted : 0,
      is_locked: 0,
      enable_trade: 1,
      registration_fee:0
    };
    if(this.cliMarketWalletList.some(e=>e.client_id===obj.client_id && e.market_id===item.market_id)){
      alertify.error('Record Already Exist');
    } else {
      this.cliMarketWalletList.push(obj);
    }

  }

  marketParams(){
    let client_id = this.clientApi.clientSelectModel.id || 0,
    wallet_id = this.selectedWallet['wallet_id'] || 0;
    if(client_id == 0 || wallet_id == 0){
      return;
    }

    let obj = {
      client_id:client_id,
      wallet_id:wallet_id,
      pnl_id : 2
    };
    return obj;
  }

  marketWalletParams(){
    let client_id = this.clientApi.clientSelectModel.id || 0,
    wallet_id = this.selectedWallet['wallet_id'] || 0;
    if(client_id == 0 || wallet_id == 0){
      return;
    }

    let obj = {
      client_id:client_id,
      wallet_id:wallet_id,
      pnl_id : 1
    };
    return obj;

  }
  saveWallet(){

      let newRecords = this.cliMarketWalletList.filter(function(item){
        return item.is_new == 1;
      });

      let deletedRecords = this.cliMarketWalletList.filter(function(item){
        return item.is_deleted==1;
      });

      let records = {new_records:JSON.stringify(newRecords),deleted_records:JSON.stringify(deletedRecords)};
      this.clientApi.saveClientWallet(records).subscribe(response => {
        if(response['success']){
          alertify.success(response['message']);
          this.getClientMarketWalletList(this.marketWalletParams());
        }
        else{
          alertify.error(response['message']);
        }
        //TODO
      });
  }

  onOffStatusChange(id)
  {
    let controllerName = 'client-market-wallet';
    this.clientApi.toggleStatus(id,controllerName).subscribe(res=>{
        if(res['success']==true)
        {
          alertify.success(res['message']);
        }
        else{
          alertify.error(res['message']);
          this.getClientMarketList(this.marketParams());
        }
      });
  }

  onOffTradeStatusChange(id)
  {
    let controllerName = 'client-market-wallet';
    this.clientApi.toggleTradeStatus(id,controllerName).subscribe(res=>{
      if(res['success']==true)
      {
        alertify.success(res['message']);
      }
      else{
        alertify.error(res['message']);
        this.getClientMarketList(this.marketParams());
      }
    });
  }

  editItem(id) {
    this.route.navigate([`client/marketWalletList/edit/${id}`]);
  }

  onClientChange(){
    this.activeRowIdx = 0;
    this.showWalletLoadingGif=true;
    this.getWalletList();
    // const config = this.marketParams();
    // // console.log(this.clientApi.clientSelectModel);
    // // this.selectedClient = this.clientList[record];
    // this.getClientMarketList(this.marketParams());
    // this.getClientMarketWalletList(this.marketParams());
    // this.showLoadingGIf = true;
  }
}
