import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';

import { ClientListComponent } from './client-list/client-list.component';
import { ClientFormComponent } from './client-form/client-form.component';
import { ClientMarketListComponent } from './client-market-list/client-market-list.component';
import { ClientMarketFormComponent } from './client-market-form/client-market-form.component';
import { ClientMarketWalletListComponent } from './client-market-wallet-list/client-market-wallet-list.component';
import { ClientMarketWalletFormComponent } from './client-market-wallet-form/client-market-wallet-form.component';
import { ClientWalletListComponent } from './client-wallet-list/client-wallet-list.component';
import { ClientWalletFormComponent } from './client-wallet-form/client-wallet-form.component';

const routes: Routes = [
	{ path: '', redirectTo: 'list', pathMatch : 'full' },
	{ path: 'list', component : ClientListComponent, pathMatch : 'prefix' },
	{ path: 'new', component : ClientFormComponent, pathMatch : 'prefix' },
	{ path: 'edit/:id', component : ClientFormComponent, pathMatch : 'prefix' },
	{ path: 'delete/:id', component : ClientListComponent, pathMatch : 'prefix' },
	{ path: 'marketList', component : ClientMarketListComponent, pathMatch : 'prefix' },
	
	{ path: 'marketList/:id', component : ClientMarketListComponent, pathMatch : 'prefix' },

	{ path: 'market/new', component : ClientMarketFormComponent, pathMatch : 'prefix' },
	{ path: 'client/market/edit/:id', component : ClientMarketFormComponent, pathMatch : 'prefix' },
	{ path: 'client/marketWalletList/edit/:id', component : ClientMarketWalletFormComponent, pathMatch : 'prefix' },
	{ path: 'market/delete/:id', component : ClientMarketListComponent, pathMatch : 'prefix' },
	{ path: 'marketWalletList', component : ClientMarketWalletListComponent, pathMatch : 'prefix' },
	{ path: 'marketWallet/new', component : ClientMarketWalletListComponent, pathMatch : 'prefix' },
	{ path: 'marketWallet/edit/:id', component : ClientMarketWalletListComponent, pathMatch : 'prefix' },
	
	{ path: 'marketWalletList/:id', component : ClientMarketWalletListComponent, pathMatch : 'prefix' },

	{ path: 'marketWallet/delete/:id', component : ClientMarketWalletListComponent, pathMatch : 'prefix' },

	{ path: 'walletList', component : ClientWalletListComponent, pathMatch :'prefix' },

	{ path: 'walletList/:id', component : ClientWalletListComponent, pathMatch :'prefix' },

	{ path: 'wallet/new', component : ClientWalletFormComponent, pathMatch : 'prefix' },
	{ path: 'client/wallet/edit/:id', component : ClientWalletFormComponent, pathMatch : 'prefix' },
	{ path: 'wallet/delete/:id', component : ClientWalletListComponent, pathMatch : 'prefix' },	

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ClientRoutingModule { }
